import React,{Component, useEffect, useState} from 'react'
import Product from '../Components/Product'
import firebase from '../Config/Firebase'
import IButton from '../Components/IButton'
import {getProducts} from "../Services/ItemServices"

function Home(props) {
  const [products, setProducts] = useState([])
  const [loading, setLoading] = useState(true)
  const [form, setForm] = useState({name:'', price:'', description:'', image:'', stock:''})
  const [buttonText, setButtonText] = useState("ADD ITEM")
  const [buttonColor, setButtonColor] = useState({backgroundColor:"#0088a9"})
  const [visibility, setVisibility] = useState({opacity:0})
  const [hidden, setHidden] = useState(true)

  useEffect(
    ()=>{
      firebase.db.collection('products')
      .get()
      .then(querySnapshot=>{
        console.log(querySnapshot.docs)
        setProducts(querySnapshot.docs)
        setLoading(false)
      })
    },
    []
  )

  const handleChange = (e) => {
    const name = e.target.name
    const value = e.target.value
    console.log(name, value)
    setForm({...form,[name]:value})
  }

  const handleSumbit = (e) => {
    e.preventDefault()
    firebase.db.collection("products")
    .add({
      name:form.name,
      description:form.description,
      price:form.price,
      image:form.image,
      stock:form.stock,
      sales:0
    },{merge:true})

    .then(doc=>{
      console.log("Doc", doc)
      setButtonColor({backgroundColor:"green"})
      setButtonText("ITTEM ADDED")
    })

    .catch(error=>{
      console.log("Error", error)
      setButtonColor({backgroundColor:"rgb(136, 0, 0)"})
      setButtonText("ITEM NOT ADDED")
    })
  }

  const showEditCard = () => {
    if (hidden) {
      setVisibility({opacity:1})
      setHidden(false)
    } else {
      setVisibility({opacity:0})
      setHidden(true)
    }
  }
  
  if (loading) {
    return(
      <p className="loader">cargando...</p>
    )
  } 
  
  else {
    return( 
      <div className="home">
        <p className="home_title">Products List</p>
        {products.map(product=><Product id={product.id} data={product.data()} />)}
        <div className="buttonWico-home_card">
          <IButton text="| Add" icon="fas fa-plus-circle" scndClass="buttonWico-home" click={showEditCard}/>
        </div>

        <div className="add_card" style={visibility}>
          <div className="add_item">
            <form onSubmit={handleSumbit} className="add_form">
                <input type="text" name="image" value={form.image} onChange={handleChange} className="add_input" placeholder="Image (Enter the URL)"></input>
                <input type="number" min="1" name="stock" value={form.stock} onChange={handleChange} className="add_input" placeholder="Stock"></input>
                <input type="text" name="name" value={form.name} onChange={handleChange} className="add_input" placeholder="Name"></input>
                <input type="text" name="description" value={form.description} onChange={handleChange} className="add_input" placeholder="Description"></input>
                <input type="number" min="1" name="price" value={form.price} onChange={handleChange} className="add_input" placeholder="Price"></input>
              <button type="sumbit" className="save" style={buttonColor}>{buttonText}</button>
            </form>
          </div>
        </div>
      </div>
    )
  } 

}

export default Home; 

